












import Vue from 'vue';
import { NavBar } from 'vant';
import ynowApi from '../../api/ynow';
Vue.use(NavBar);

export default {
  data () {
    return {
      list: []
    };
  },
  mounted () {
    ynowApi.getCheckinList().then(res => {
      this.list = res.data.list;
    });
  },
  methods: {
    onClickLeft () {
      this.$router.go(-1);
    }
  }
};



